'use strict';

/**
 * dismantles unnecissary lists
 * @method single
 *
 * @param {Array} list - list of items
 *
 * @return {Array|Object}
 */
function single (list) {
  var result;
  var length;

  length = list.length;
  result = length === 1 ? list.shift() : list;

  return result
}

module.exports = single;
